<template>
    <div class="home">
        <p class="title">骑士君，在这里输入你的简介～</p>
        <p class="sub"><i class="el-icon-warning"></i>包含颜色代码在内，简介限制60个字符。设置了渐变色后，位于渐变色前方的字体必须设置颜色，否则在游戏内会以纯白的形式显示</p>
        <el-form ref="form1" label-width="80px">
            <el-form-item label="简介">
                <el-input
                    id="introInput"
                    type="textarea"
                    :rows="3"
                    placeholder="请输入简介"
                    v-model="textIntro"
                    :autosize="{minRows: 3, maxRos: 6}"
                    :maxlength="60"
                    show-word-limit
                    >
                </el-input>
            </el-form-item>
            <el-form-item label="更改颜色">
                <el-button type="primary" @click="openGradientColorDialog">
                    设置渐变色
                </el-button>
            </el-form-item>
            <el-form-item label="纯色">
                <el-button @click="handleClick" style="padding: 0px;border: none">
                    <el-color-picker 
                        :class="{'is-hidden': !isInPlainColor}"
                        id="plain"
                        v-model="plainColor" 
                        color-format="hex"
                        @change="changePlainColor"
                        >
                    </el-color-picker>
                </el-button>
            </el-form-item>
            <el-form-item label="字体">
                <el-button type="primary" @click="change('bold')">加粗</el-button>
                <el-button type="primary" @click="change('sup')">上移</el-button>
                <el-button type="primary" @click="change('sub')">下移</el-button>
                <el-button type="primary" @click="change('slash')">横线</el-button>
                <el-button type="primary" @click="change('italic')">倾斜</el-button>
            </el-form-item>
        </el-form>
        <div style="text-align: center">
            <el-button type="success" @click="copyCodeToClipBoard">
                复制代码
            </el-button>
        </div>
        <div class="img-holder">
            <img src="https://konfan.oss-cn-beijing.aliyuncs.com/pcr/kitty.png" alt="">
        </div>
        <div class="balabala">
            本网站可以用来在公主连接的大多数输入框中生成类似于富文本的效果。但是搞不好哪天B站更新后台，这些方式可能都不但能用了。
            <a href="https://yuiYuan.xyz" target="_blank">关于作者</a>
        </div>
        <!-- dialog -->
        <el-dialog
            title="设置渐变色"
            :visible.sync="isColorDialogOpen"
            width="30%"
            :before-close="handleClose">
            <el-form>
                <el-form-item label="起始色">
                    <div class="block">
                        <el-color-picker v-model="fromColor" color-format="hex"></el-color-picker>
                    </div>
                </el-form-item>
                <el-form-item label="终止色">
                    <div class="block">
                        <el-color-picker v-model="toColor" color-format="hex"></el-color-picker>
                    </div>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="cancelNClose">取 消</el-button>
                <el-button type="primary" @click="applyNclose">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>

export default {
    name: 'Home',
    data() {
        return {
            isColorDialogOpen: false,
            fromColor: "#000000",
            toColor: "#000000",
            plainColor: "",
            textIntro: "",
            codeIntro: "",
            tempSelectionText: "",
            tempSelectionTextStart: 0,
            isInPlainColor: false
        }
    },
    methods: {
        copyCodeToClipBoard() {
            const input = document.createElement('input');
            document.body.appendChild(input);
            input.setAttribute('value', `${this.textIntro}`);
            input.select();
            if (document.execCommand('copy')) {
                document.execCommand('copy');
                this.$message.success('已复制到剪贴板');
            }
            document.body.removeChild(input);
        },
        transText(type, rawText) {
            if (rawText) {
                switch (type) {
                    case 'bold': {
                        return `[b[b]]${rawText}[/b]`
                    }
                    case 'sup': {
                        return `[sup[b]]${rawText}[/sup]`
                    }
                    case 'sub': {
                        return `[sub[b]]${rawText}[/sub]`
                    }
                    case 'slash': {
                        return `[s[b]]${rawText}[/s]`
                    }
                    case 'italic': {
                        return `[i[b]]${rawText}[/i]`
                    }
                }
            }
        },
        change(type) {
            const text = window.getSelection().toString();
            if (!text) {
                this.$message.error("请选择文字")
                return
            }
            const start = introInput.selectionStart;
            const end = introInput.selectionEnd;
            const deleteCount = end - start;
            const replacement = this.transText(type, text)
            const temp = this.textIntro;
            this.textIntro = temp.substring(0, start) + replacement + temp.substr(start + text.length);
            this.codeIntro = this.textIntro;
        },
        changeEndTag(bool) {
            if (!bool) {
                this.codeName = this.displayName.replace(/\[\/b]/g, "")
                .replace(/\[\/i]/g, "")
                .replace(/\[\/del]/g, "")
                .replace(/\[\/sub]/g, "")
                .replace(/\[\/sup]/g, "");
            }
        },
        openGradientColorDialog() {
            this.tempSelectionText = window.getSelection().toString();
            if (!this.tempSelectionText) {
                this.$message.error("请选择文字")
                return
            }
            this.tempSelectionTextStart = introInput.selectionStart;
            this.isColorDialogOpen = true;
        },
        getGradient() {
            const text = window.getSelection().toString();
            return `[${this.fromColor.replace("#", "").toLowerCase()},${this.toColor.replace("#", "").toLowerCase()}]` 
        },
        gradient() {
            const text = this.tempSelectionText;
            const start = this.tempSelectionTextStart;
            if (!this.textIntro.includes("[-]")) {
                this.codeIntro = this.textIntro.substring(0, start) + "[-]" + this.getGradient() + this.textIntro.substring(start);
            }
            else {
                this.codeIntro = this.textIntro.substring(0, start) + this.getGradient() + this.textIntro.substring(start);
            }
            this.textIntro = this.codeIntro;
            this.clearTemp();
        },
        handleClick() {
            this.tempSelectionText = window.getSelection().toString();
            if (!this.tempSelectionText) {
                this.$message.error("请选择文字");
                return
            }
            this.tempSelectionTextStart = introInput.selectionStart;
            this.isInPlainColor = true;
        },
        changePlainColor(val) {
            const start = this.tempSelectionTextStart;
            if (!this.textIntro.includes("[c[b]]")) {
                this.codeIntro = this.textIntro.substring(0, start) + "[c[b]]" + `[${val.replace("#", "")}[b]]` + this.textIntro.substring(start);
            }
            else {
                this.codeIntro = this.textIntro.substring(0, start) + `[${val.replace("#", "")}[b]]` + this.textIntro.substring(start);
            }
            this.textIntro = this.codeIntro;
            this.clearTemp();
        },
        clearTemp() {
            this.tempSelectionText = "";
            this.tempSelectionTextStart = 0;
        },
        applyNclose() {
            this.gradient();
            this.isColorDialogOpen = false;
        },
        cancelNClose() {
            this.isColorDialogOpen = false;
        },
        handleClose() {
            this.isColorDialogOpen = false;
            this.clearTemp();
        }
    }
}
</script>

